
import { onMounted } from "@vue/runtime-core"
import { useStore, Store } from "vuex";
import { defineComponent, reactive } from "vue";

export default defineComponent ({
  name: "NewRoomPrivate",
  data() {
    return {
      name: "" as string,
      file: [] as any[],
      created: false as boolean,
      // currentUser: useStore().getters.whoAmI,
    };
  },
  setup()
  {
    let thechannels = reactive([] as any[]); // TODO check type
    const store = reactive(useStore() as Store<any>);
    const socketVal = reactive(store.getters.getSocketVal as any); // TODO check type

    onMounted(() =>{
      socketVal.on("channel", function(res: any) { // TODO precise type of channels/img
        console.log('befor update');
        console.log(thechannels);
        console.log('creating channel');

          // reset channel
          thechannels = [];
           for (const channel of res) {

            var data = {} as any; //beark :(
            data.title = channel.channelName;
            let blob = new Blob([channel.avatar], {type: 'image/bmp'});
            if(blob.size !== 2)
            {
              let image = new Image();
              image.src = URL.createObjectURL(blob);
              data.avatar = image.src;
            }
            else
              data.avatar = "data:image/jpeg;base64,"
//            console.log(data.avatar)
            thechannels.push(data);
          }
        console.log('after update');
        console.log("!!!!!!!!!!!!!!!!!!!!!!!!!!!");
        store.commit('setChannels' , thechannels);
        console.log(store.getters.getChannels);
      })
    })

    function createError()
    {
        alert("YOU DIDN'T SPECIFY NAME - NOTHING WILL BE CREATED")
    }

    function submitIt(name: string, file: any)
    {
      const password = "";
      const publ = false;
      // const user = this.currentUser;
      console.log("name: " + name)
      if (name == '')
        createError()
      else
        socketVal.emit('createChannel', {channelName: name, users: [], password, publicChannel: publ, avatar: file});    
    }

    return { submitIt }
  },
 methods: {
    submitbutton() {
      // console.log(this.created);
      console.log(this.name);
      console.log(this.file);
      this.created = true;
    },
    previewFiles(event: any) { // TODO check type of event
        this.file = event.target.files[0];
        console.log(event.target.files[0]);
    },
  },
})
